.Icon-container{
    cursor:pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    height:48px;
    width:48px;
    font-size:24px;
    font-weight:700
}

.Icon-svg{
    width: 24px;
    height: 24px;
}

.Icon-container-delete:hover .Icon-svg{
    fill:red
}

.Icon-container-complete:hover .Icon-svg{
    fill:green
}