.LoadingToDo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0);
    border-radius: 10px;
    padding: 12px 0;
}

.LoadingToDo-text{
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingToDo-completeIcon, .LoadingToDo-deleteIcon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    height: 48px;
    width: 48px;
}

.LoadingToDo-completeIcon{
    position: absolute;
    left: 12px;
}

.LoadingToDo-deleteIcon{
    position: absolute;
    top: 12px;
    right: 12px;
}

.LoadingToDo-container, .LoadingToDo-completeIcon, .LoadingToDo-deleteIcon{
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200,199,199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}